// @flow
import React from 'react';
import type { Element } from 'react';
import Loadable from 'react-loadable';
import _get from 'lodash/get';
import { OFFER_STEPS, OFFER_STEPS_SUBMIT } from 'shared/Consts';
import type { Offer } from 'types/Offer';

type Props = {
  offer: Offer,
};

const StatusLoadable = ({ offer }: Props): Element<typeof Loadable> => {
  const stepId = _get(offer, ['StepId']);
  // in case component status not existed
  const status = _get(offer, ['Step', 'messages', 'buyer'], '-');

  // Combine step 1 with step 150
  const componentId = OFFER_STEPS_SUBMIT.includes(stepId)
    ? OFFER_STEPS.pleaseComplete
    : stepId;

  const Status = Loadable({
    loader: () => {
      return import(`components/BuyerOfferStep/Step${componentId}/Status`);
    },
    loading() {
      return <span>{status}</span>;
    },
  });

  return <Status offer={offer} />;
};

export default StatusLoadable;
